import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'codeFormat'})
export class CodeFormatPipe implements PipeTransform {
    transform(resultBody: string): string {
        if (resultBody != null && resultBody !== '') {
            // let temp: string = resultBody.toString().replace(/;/g, ";<br/>");
            let temp: string = resultBody.toString().replace(/\n/g, '');
            // temp = temp.replace(/\./g, "<br/>&nbsp;&nbsp;&nbsp;&nbsp;.");
            temp = temp.replace(/; /g, ';');
            temp = temp.replace(/;/g, ';\n');
            temp = temp.replace(/\. /g, '.');
            temp = temp.replace(/\./g, '\n   .');
            temp = temp.replace(/^"|"$/g, '');
            console.log(temp);
            return temp;
        } else {
            return '';
        }
    }
}
